import React, { FC } from 'react';
import cn from 'classnames';
import styles from './footer.module.scss';
import { useAppDispatch } from '../../store/hooks';
import { actions } from '../../store/game/slice';
import WinnersPt from '../../game/components/winners-pt';
import Button from '../../common/components/button';

const FooterPt: FC<any> = () => {
  const dispatch = useAppDispatch();
  
  const handleClick = () => {
    dispatch(actions.setIsWinnersVisible(true));
  }

  return (
    <div className={cn(styles.winners)}>
      <Button outlined={true} onClick={() => handleClick()}>nossos sortudos vencedores</Button>
      <WinnersPt />
    </div>
  );
};

export default FooterPt;
