import React, { FC } from 'react';
import cn from 'classnames';

import HeaderPt from '../header-pt/';
import styles from './layout.module.scss';
import FooterPt from '../footer-pt';

type LayoutProps = JSX.IntrinsicElements['div'];

const LayoutPt: FC<LayoutProps> = ({children }: LayoutProps) => {
  return (
    <div className={cn(styles.wrapper)}>
      <HeaderPt />
      <main className={cn(styles.main)}>
        <div className={cn(styles.container)}>{children}</div>
      </main>
      <FooterPt />
    </div>
  );
};

export default LayoutPt;
